import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivityClassworkItemResolver } from './activity-classwork.resolver';

import { ActivityClassworkComponent } from './activity-classwork.component';

const routes: Routes = [
  { path: 'list', component: ActivityClassworkComponent},
  { path: 'list/details/:id',
    loadChildren: () => import('src/app/modules/admin/activity/activity-classwork/activity-classwork-item/activity-classwork-item.module').then(m => m.ActivityClassworkItemModule),
    resolve: {
      ActivityClassworkItemResolver
    }
  },
  { path: '', redirectTo: 'list', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityClassworkRoutingModule { }
