import { Component, Input, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';

import { BatchService } from 'src/app/swagger/api/controllers/Batch';

import { ActivityClassworkService } from './activity-classwork.service';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as moment from 'moment';

@Component({
  selector: 'activity-classwork',
  templateUrl: './activity-classwork.component.html',
  styleUrls: ['./activity-classwork.component.scss']
})
export class ActivityClassworkComponent implements OnInit {

  addClassWork:boolean = false;
  isClassWorkAdded:boolean = false;
  openProgramBatchTypeList:any = [];

  gradeDataList:any = [];

  _activityList:any = [];
  fullActivityList:any = [];

  searchData:FormControl;

  search:string = '';
  apiSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any = 0;
  itemLimit: any = 5;

  allData: any;

  message: any;

  activity:any = {}

  constructor(
    private _router: Router,
    private batchService: BatchService,
    private activityClassworkService: ActivityClassworkService,
    private sessionService: SessionService,
    private sharedService:SharedService
  ) {


    this.message = null;

    this._activityList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

  }

  get activity$(): Observable<any>{
    return this._activityList.asObservable();
  }


  showAddClassWork() {
    this.addClassWork = true
  }

  hideAddClassWork() {
    this.addClassWork = false
  }

  onBatchTypeChange(event) {
    this.activity.batchTypeId = event[0].id
  }

  onGradeChange(event) {
    this.activity.gradeId = event[0].id
  }

  getactivityDueDate(event) {
    this.activity.dueDate = event
  }

  submitAddClassWorkForm() {

    this.isClassWorkAdded = false
    this.addClassWork = false

    setTimeout(() => {
      this.isClassWorkAdded = true
    }, 1000)


  }

  getDate(date) {
    return moment(date).format('Do MMM YYYY');
  }

  gotoActivityDetails(activity) {
    this.activityClassworkService.setClassWorkActivityData(activity)
    this._router.navigate(['/activity/classwork/list/details', activity.id])
  }

  ngOnInit(): void {

    let res = [
      {
        "id": 1,
        "name": "ABCD Activity - Test 2 March",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "dueDate": "2021-04-06T08:57:32.270Z",
        "comments": 2,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1
      },
      {
        "id": 2,
        "name": "New Activity - Test 2 March",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "dueDate": "2021-04-06T08:57:32.270Z",
        "comments": 2,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1
      },
      {
        "id": 3,
        "name": "New Activity - Test 2 March",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "dueDate": "2021-04-06T08:57:32.270Z",
        "comments": 2,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1
      }
    ];

    this.fullActivityList = res;
    this.allData = res;
    this._activityList.next(res);

    this.activity$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if(this.totalItems > 0) {
        this.isClassWorkAdded = true
      }

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    })

    // Subscribe to search input field value changes
    this.searchData.valueChanges
    .pipe(
      startWith(''),
      map((val) => {

        let newData = this.fullActivityList.filter(item => {
          for (let field in item) {
            if (item[field] === null || item[field] === undefined) {
              continue;
            }
            if (item[field].toString().toLowerCase().indexOf(val.toString().toLowerCase()) !== -1) {
              return item;
            }
          }
        })

        this._activityList.next(newData);


      })
    )
    .subscribe();

    let params = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

     this.batchService.getBatchCategoryList(params).subscribe((res:any) => {

      if(res.value) {

        this.openProgramBatchTypeList = res.value.filter(item => {
          return item.active
        });

      }

     });

  }

}
