import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { ActivityClassworkService } from './activity-classwork.service';


@Injectable({
  providedIn: 'root'
})
export class ActivityClassworkItemResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private activityClassworkService: ActivityClassworkService,
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    var data = this.activityClassworkService.getClassWorkActivityData();

    if(data == null){

      // Navigate to parent url
      this.router.navigateByUrl('/activity/classwork/list');

    }


    return;

  }

}

