import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ActivityClassworkService {

  private classWorkActivityData = new BehaviorSubject<any>(null);
  classworkactivitydatacast = this.classWorkActivityData.asObservable();

  private classWorkActivityStudentData = new BehaviorSubject<any>(null);
  classworkactivitystudentdatacast = this.classWorkActivityStudentData.asObservable();

  constructor() { }

  getClassWorkActivityData(){
    return this.classWorkActivityData.value
  }

  setClassWorkActivityData(value){
    this.classWorkActivityData.next(value);
  }

  getClassWorkActivityStudentData(){
    return this.classWorkActivityStudentData.value
  }

  setClassWorkActivityStudentData(value){
    this.classWorkActivityStudentData.next(value);
  }

}
